@media (max-width: 950px) {
  .container {
    flex-direction: column;
  }
  .swiper__container {
    width: 100%;
  }
  .card__container {
    width: 100%;
    padding: 0;
  }
  .flip__container {
    /* width: min(64.4vw, 494px); */
    width: 100%;
  }
